let _this,searchParams = {
  sortType: 1,
  sortName: 'lastUpdateTime',
  enabledPaging: true,
  page: 1,
  perPage: 10,
};
import searchComponent from '../commonComponents/searchBar.vue'
import paging from '../commonComponents/paging.vue'
import filterList from '../commonComponents/filterParamsList.vue'
export default {
  name: "vehicle-report",
  components:{searchComponent,paging,filterList},
  data(){
    return{
      isShrink: true,
      paramsData: {searchInfo: []}, /*搜索参数列表*/
      searchData: {
        searchInfo: [{
          title: 'License Plate:',
          filed: 'plateNumber',
          keyword: '',
          type: 'input',
        }, {
          title: 'Vehicle Type:',
          filed: 'plateTypes',
          keyword: [],
          type: 'select-search-multiple',
          selectData: this.common.plateType,
          text: 'label',
          val: 'value',
        }/*,{
          title: "Verified Time:",
          type: "date-group",
          keyword: '',
          content: [{filed: "startLastUpdateTime", keyword: ""}, {filed: "endLastUpdateTime", keyword: ""}]
        }*/]
      },
      titleData: [
        {title: 'License Plate', key: 'plateNumber', minWidth: 180},
        {title: 'Validity', slot: 'validity', minWidth: 200},
        {title: 'Vehicle Type', slot: 'plateType', minWidth: 150},
        {title: 'Last Update', slot: 'lastUpdateTime', minWidth: 170,sortable: 'custom', sortType: 'decs'}],
      tableData:[],
      pageInfo: {
        curPage: '',
        curPageVal: 1,
        isShow: '',
        total: 1,
        currentPage: 1,
        limit: 10,
        isLimit: false,
        itemCount: 0,
      },
      searchParams:JSON.parse(JSON.stringify(searchParams))
    }
  },
  mounted(){
    _this = this;
    this.getTableData();
  },
  methods:{
    // 查询
    getSearch: function (searchParamsData) {
      let searchParamsItem = JSON.parse(JSON.stringify(searchParams));
      for (var key in searchParamsData) {
        searchParamsItem[key] = searchParamsData[key];
      }
      this.searchParams = searchParamsItem;
      this.searchParams.page = 1;
      this.searchParams.perPage =  _this.pageInfo.limit;
      localStorage.setItem('searchData',JSON.stringify(this.searchData));
      _this.paramsData.searchInfo = JSON.stringify(searchParamsData) === '{}' ? [] : JSON.parse(JSON.stringify(_this.searchData.searchInfo));
      _this.getTableData();
    },
    getTableData:function () {
      this.$ajax.post('/api/PlateInfo/Paginate',_this.searchParams).then(function (res) {
        if (res.data.success) {
          if (res.data.data.pageCount > 0 && (_this.searchParams.page > res.data.data.pageCount)) { //当删除时到最后一页，则将页数变为总页数，并重新请求
            _this.searchParams.page = res.data.data.pageCount;
            _this.getTableData();
          } else if (res.data.data.pageCount <= 0) {
            _this.searchParams.page = 1;
            _this.pageInfo.total = 1;
          } else if (res.data.data.pageCount > 0) {
            _this.pageInfo.total = res.data.data.pageCount;
          }
          _this.pageInfo.currentPage = res.data.data.page;
          _this.pageInfo.curPageVal = _this.pageInfo.currentPage;
          _this.pageInfo.itemCount = res.data.data.itemCount;
          _this.tableData = res.data.data.items;
          _this.$root.getTableMaxHeight();
        } else {
          _this.messageNotice('error', res.data.message);
        }
      })
    },
    // 过滤列表搜索
    getSearData: function (data) {
      _this.searchData = JSON.parse(JSON.stringify(data));
      _this.$refs.searchBar.refactoringData(data);
    },
    // 清除所有参数
    clearAll: function () {
      _this.$refs.searchBar.reset(_this.searchData);
    },
    // 列表排序
    sortTable: function (data) {
      let tableOrder = 'desc';
      /*列表排序，用于记录上次排序*/
      //如果点击后排序为normal，即无排序，就手动排序为asc
      if (data.order == 'normal') {
        let index = -1;
        //handleSort需要index参数来确定是第几列，使用columns和data.column的key值对比来查询
        this.$refs.utmTable.columns.some((item, i) => {
          if (item.title == data.column.title) {
            index = i;
            return true
          }
        });
        //index不为-1，则手动调用handleSort，这里this.$refs.table指向表格组件，需要在表格中写ref
        tableOrder = tableOrder == 'asc' ? 'desc' : 'asc';
        index && this.$refs.utmTable.handleSort(index, tableOrder);
      } else {
        tableOrder = data.order;
        _this.searchParams.sortName = data.column.slot;
        _this.searchParams.sortType = tableOrder === 'asc' ? 0 : 1;
        _this.getTableData();
      }
    },
    // 翻页
    getCurrentPage: function (data) {
      _this.searchParams.page = _this.pageInfo.curPage;
      _this.searchParams.perPage = _this.pageInfo.limit;
      _this.getTableData();
    },
  }
}



