let _this;
export default {
  name: "systemSetting",
  data(){
    return {
      systemInfo:{}
    }
  },
  mounted(){
    _this = this;
    this.getSystemInfo();
  },
  methods:{
    getSystemInfo:function () {
      this.$ajax.get('/api/SystemSetting/Get').then(function (res) {
        if(res.data.success){
          _this.systemInfo = res.data.data;
        }else{
          _this.messageNotice('error',res.data.message);
        }
      })
    },
    judgeValue:function (str,limit) {
      if(!/\d/.test(_this.systemInfo[str]) || _this.systemInfo[str] <= 0 || (limit && _this.systemInfo[str] > limit)){
        _this.systemInfo[str] = '';
      }
    },
    saveInfo:function () {
      this.$validator.validateAll('systemInfo').then(result =>{
        if(result){
          _this.$ajax.put('/api/SystemSetting/Update',_this.systemInfo).then(function (res) {
            if(res.data.success){
              _this.messageNotice('success');
              _this.getSystemInfo();
            }else{
              _this.messageNotice('error',res.data.message);
            }
          })
        }
      })
    },
  }
}



