export default {
  name: "paging",
  props:['pageInfo'],
  data(){
    return{
      isMoreTable:false,
      limitList:[5, 10, 15, 20, 25, 30, 35, 40, 45, 50, 100, 200],
      cn:{
        firstbtn:'首页',
        prebtn:'上一页',
        nextbtn:'下一页',
        lastbtn:'尾页',
        gobtn:'确定',
        first:'这已经是首页了',
        pre:'这已经是第一页了',
        next:'这已经是最后一页了',
        last:'这已经是尾页了',
        go:'请输入正确的页数',
      },
      en:{
        firstbtn:'First',
        prebtn:'Previous',
        nextbtn:'Next',
        lastbtn:'Last',
        gobtn:'Go',
        first:'Is the first page',
        pre:'Is the first page',
        next:'Is the last page',
        last:'Is the last page',
        go:'Please enter a right page',
      }
    }
  },
  methods:{
    getTableMaxHeight:function () {
      if(this.pageInfo.limit>10){
        this.isMoreTable = true
      }else {
        this.isMoreTable = false
      }
    },
    firstPage(data){//首页
      if(data.currentPage != 1){
        data.curPage = 1;
        this.$emit('getCurPage',data);
      }else{
        data.isShow = 'first';
        var lineTime = setTimeout(function (){
          clearTimeout(lineTime);
          if(data.isShow == 'first'){
            data.isShow = '';
          }
        },1500);
        return false;
      }
    },
    prePage(data){//上一页
      if(data.currentPage > 1){
        data.curPage = (data.currentPage/1)-1;
        this.$emit('getCurPage',data);
      }else{
        data.isShow = 'pre';
        var lineTime = setTimeout(function (){
          clearTimeout(lineTime);
          if(data.isShow=='pre'){
            data.isShow = '';
          }
        },1500);
        return false;
      }
    },
    nextPage(data){//下一页
      if(data.currentPage < data.total){
        data.curPage = (data.currentPage/1)+1;
        this.$emit('getCurPage',data);
      }else{
        data.isShow = 'next';
        var lineTime = setTimeout(function (){
          clearTimeout(lineTime);
          if(data.isShow=='next'){
            data.isShow = '';
          }
        },1500);
        return false;
      }
    },
    lastPage(data){//最后一页
      if(data.currentPage != data.total){
        data.curPage = data.total;
        this.$emit('getCurPage', data);
      }else{
        data.isShow = 'last';
        var lineTime = setTimeout(function (){
          clearTimeout(lineTime);
          if(data.isShow=='last'){
            data.isShow = '';
          }
        },1500);
        return false;
      }
    },
    goToPage(data){//跳转
      if((data.curPageVal/1) < (data.total+1) && data.curPageVal != null && data.curPageVal != '' && (data.curPageVal/1)>0 && /^-?\d+$/.test(data.curPageVal)){
        data.curPage = data.curPageVal;
        this.$emit('getCurPage',data);
        $('#targetPage').val('');
      }else{
        data.isShow = 'goTo';
        var lineTime = setTimeout(function (){
          clearTimeout(lineTime);
          if(data.isShow == 'goTo'){
            data.isShow = '';
          }
        },1500);
        return false;
      }
    },
    selectLimit:function(data){
      data.curPage = 1;
      this.$emit('getCurPage',data);
    },
  }
}



