let _this;
import searchComponent from '../commonComponents/searchBar.vue'
import vehicleReport from './vehicleReport.vue'
import verificationReport from './verificationReport.vue'
export default {
  name: "report",
  components:{
    searchComponent,
    vehicleReport,
    verificationReport
  },
  data(){
    return {
      tabIndex: 0,
      tabList: ['Verification Report','Vehicle Report']
    }
  },
  mounted(){
    _this = this;
  },
  methods:{

  }
}



