export default {
  name: "filter-params-list",
  props:['searchData','isReset'],
  data(){
    return{
      timeOptions:{ /*通行时间类型*/
        0:'A Period Of Time',
        1:'Office Hour',
        2:'After Office Hour',
        3:'Non-working Day',
        4:'Customize'
      },
    }
  },
  methods:{
    // 删除当前搜索参数
    deleteField:function (field) {
      if(field.params){
        field.params = [];
      }
      field.keyword = '';
      if(field.type === 'time-panel'){
        field.oldKeyword = {
          type: '',
            periodOfTime: {
            startDateTime: '',
              endDateTime: ''
          },
          customize: [
            {
              weeks: [0],
              startTime: '',
              endTime: ''
            }
          ]
        }
      }
      else if(field.type === 'date-group'){
        field.content.forEach(date =>{
          date.keyword = '';
        })
      }
      else if(field.type === 'select-input'){
        field.content[0].keyword = '';
        field.content[1].keyword = '';
      }
      this.$emit('getSearData',this.searchData);
    },
    // 清除所有参数
    clearAll:function () {
      this.$parent.clearAll();
    },
  }
}



